.ulList {
   list-style: none; 
   margin: 0;
   padding: 0;
   display: flex;
   flex-wrap: wrap;
   justify-content: flex-start;
   overflow-y: scroll; 
   height: 520px;
   padding-bottom: 40px;
   -ms-overflow-style: none;  
   scrollbar-width: none; 
}

.title {
    font-family: 'Manrope', Courier, monospace;
    font-size: 25px;
    display: inline-block;
    font-weight: 300;
    margin-bottom: 20px
}

.windowBlur {
    position: absolute;
    height: 100%;
    width: 100%;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(88, 88, 88, 0.506);
    z-index: 5;
    backdrop-filter: blur(5px);
}

.windowBox {
    border-radius: 5px;
    position: absolute;
    background-color: white;
    width: 240px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 15px 20px;
    z-index: 5;
}

.windowBlur.windowBlurOff {
    display: none;
}


.windowEditBlur {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    border-radius: 40px;
    z-index: 11;
    background-color: rgba(112, 112, 112, 0.506);
    backdrop-filter: blur(5px);
    display: flex;
    justify-content: center;
    align-items: center;
}

.windowEditBlurOff {
    display: none;
}

.blurSwitchOff {
    backdrop-filter: none;
    background-color: transparent;
}


.editInfoBox {
    background-color: white;
    border-radius: 5px;
    padding: 10px;
    z-index: 1;
}

.buttonsBox {
    display: flex;
    justify-content: space-around;
    margin-top: 10px;
}

.form label{
    margin: 4px 0;
}

.label1 {
    display: inline-block;
}

.input1 {
    display: inline-block;
    width: 100px;
    margin-left: 12px;
}

.input2 {
    display: inline-block;
    width: 45px;
    margin-left: 19px;
    margin-top: 10px
}

.windowBox form button {
    display: block;
    margin-top: 10px;
    width: 100%;
}

.windowBox h1 {
    padding: 0;
    margin-top: 0;
    margin-bottom: 10px;
    font-family: 'Manrope', Courier, monospace;
    font-size: 15px;
    font-weight: 300;
}

.priceInput {
    width: 40px;
}

.windowErrorBlur {
    position: absolute;
    padding: 10px 13px;
    top: 70px;
    border-radius: 5px;
    text-align: center;
    z-index: 20;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%); 
    width: 100%;
    height: 100%;
    border-radius: 40px;
    backdrop-filter: blur(4px);
    background-color: rgba(143, 143, 143, 0.506);
}

.windowErrorBlur.off {
    display: none;
}

.windowErrorBlur h1{
    font-weight: 300;
    color: rgb(0, 0, 0);
    font-size: 14px;
    padding: 0;
    margin: 0;
    font-family: "Manrope", Courier, monospace;
}

.btnOk {
    margin-top: 5px;
    padding: 4px 20px;
}

.windowInfoBlur {
    position: absolute;
    left: 50%; 
    top: 50%; 
    transform: translate(-50%, -50%);
    border-radius: 40px;
    backdrop-filter: blur(5px);
    background-color: rgba(112, 112, 112, 0.506);
    z-index: 20;
    width: 100%;
    height: 100%;
}

.windowInfoBlur.windowInfoBlurOff {
    display: none;
}

.selectt {
    overflow-y: scroll !important; /* Enable vertical scrolling */
}

.enclosed {
    height: 100px;
}

.infoBox {
    border-radius: 5px;
    width: 200px;
    padding: 10px;
    text-align: center;
    position: absolute;
    background: white;
    left: 50%; 
    top: 50%; 
    transform: translate(-50%, -50%); 
}

.infoBox h1 {
    padding: 0;
    margin: 0;
    font-weight: 300;
    font-family: "Manrope", Courier, monospace;
    font-size: 14px;
}

.infoBox button {
    margin-top: 10px;
}

.errorBox {
    padding: 10px;
    background-color: white;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -250%);
    border-radius: 5px;
    width: 150px;
    animation: twerk 500ms;
}

@keyframes twerk {
    8% {
        transform: translate(-50%, -250%) rotate(0deg);
    }
    16%{
        transform: translate(-50%, -250%) rotate(6deg);
    }
    24% {
        transform: translate(-50%, -250%) rotate(0deg);
    }
    32% {
        transform: translate(-50%, -250%) rotate(-6deg);
    }
    40% {
        transform: translate(-50%, -250%) rotate(0deg);
    }
    48% {
        transform: translate(-50%, -250%) rotate(15deg);
    }
    56% {
        transform: translate(-50%, -250%) rotate(0deg);
    }
    64%{
        transform: translate(-50%, -250%) rotate(-15deg);
    }
    72%{
        transform: translate(-50%, -250%) rotate(0deg);
    }
    80%{
        transform: translate(-50%, -250%) rotate(6deg);
    }
    88%{
        transform: translate(-50%, -250%) rotate(0deg);
    }
    96%{
        transform: translate(-50%, -250%) rotate(-6deg);
    }
    100%{
        transform: translate(-50%, -250%) rotate(0deg);
    }

}

.selectList {
    position: absolute;
    max-height: 150px;
    overflow-y: scroll;
    margin: 0;
    padding: 0;
    border-radius: 3px;
    background-color: white;
    width: 200px;
    z-index: 10;
    top: 50%;
    left: 50%; 
    transform: translate(-48%, 4%);
    box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.405);
}

.selectList.selectListOff {
    display: none;
}

.selectList ul {
    margin: 0;
    padding: 0;
}

.selectList li {
    list-style-type: none;
}

.selectItem:hover {
    background-color: rgb(219, 219, 219);
}

.selectItem {
    position: relative;
    border-bottom: 1px solid rgb(175, 175, 175);
}

.selectItem h6 {
    position: absolute;
    top: 5px;
    display: inline-block;
    font-family: 'Manrope', Courier, monospace;
    font-size: 13px;
    font-weight: 600;
    color: #5c5c5c;
}

.thumnNail {
    width: 26px;
    display: inline-block;
    padding: 0;
    margin-right: 7px;
    margin-left: 2px;
}

.overLay {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 1;
}


.selectList::-webkit-scrollbar {
    width: 6px;
}

.selectList::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey; 
    border-radius: 10px;
}

.selectList::-webkit-scrollbar-thumb {
    background: rgb(255, 134, 134); 
    border-radius: 10px;
}

.selectList::-webkit-scrollbar-thumb:hover {
    background: #b30000; 
}



